import { useState } from "react";
import { NavLink } from "react-router-dom";
import { RoomCard } from "../../components/Card/Card";
import '../../style/pages.scss'



export default function Favorite() {
    const favoriteRoomData = [
        {
            roomId: '001',
            roomTitle: '',
            roomStyle: '',
            floor: '2',
            bed: '',
            rent: 18000,
            manage:true,
            managePaid:2000,
            size: 10,
            findPerson: 1,
            address: '45',
            date: '2024/01/03',
            expect: '',
            isfavorite:true
        },
        {
            roomId: '002',
            roomTitle: '',
            roomStyle: '',
            floor: '4',
            bed: '',
            rent: 10000,
            manage:false,
            size: 7,
            findPerson: 2,
            address: '45',
            date: '2024/05/26',
            expect: '',
            isfavorite:true
        },
        {
            roomId: '003',
            roomTitle: '',
            roomStyle: '',
            floor: '2',
            bed: '',
            rent: 8000,
            manage:false,
            size: 20,
            findPerson: 1,
            address: '26',
            date: '2024/08/08',
            expect: '',
            isfavorite:true
        },
    ]
    return (
        <section className="favorite container py-5 h-100">
            {favoriteRoomData.length > 0 ? (
                <>
                    <RoomCard data={favoriteRoomData}/>
                </>
            ) : (
                <>
                <div className="d-flex flex-column justify-content-center align-items-center h-100">
                    <i className="bi bi-heartbreak-fill text-danger favoriteIcon"></i>
                    <p className="fs-1 fw-bold mb-4"></p>
                    <NavLink to='/'><button type="button" className="btn btn-primary px-5 py-3"></button></NavLink>
                </div>          
                </>
            )}
        </section>
    )
}