import FilterOffcanvas from "../../components/Offcanvas/FilterOffcanvas";
import Banner from "../../components/Banner/Banner";
import friends1 from '../../assets/images/friends.jpg';
import bannerImg1 from '../../assets/images/banner.jpg';
import { AdvantageCard } from "../../components/Card/Card";
import axios from "axios";
import { useEffect } from "react";

export default function Home() {
    const bannerDatas = [
        {
            id: 1,
            image: bannerImg1,
            showText: true,
            text: <><br /></>,
        },
        {
            id: 2,
            image: bannerImg1,
            showText: false,
            text: ''
        },
    ]

    const getData = async()=>{
        const res =  await axios.get("https://localhost:7095/API/login");
        console.log(res)
    }
    return (
        <>
            <button type="button" onClick={getData}>test</button>
            <FilterOffcanvas />
            <Banner datas={bannerDatas} />
            <section className="py-5">
                <div className="container">
                    <div className="row row-cols-1 row-cols-lg-2 gx-0 gy-3 ">
                        <div className="col text-center d-flex flex-column justify-content-center">
                            <h1></h1>
                            <h3></h3>
                            <h5></h5>
                        </div>
                        <div className="col ">
                            <figure>
                                <img src={friends1} className="object-cover w-100  rounded" alt="" />
                            </figure>
                        </div>
                    </div>
                </div>

            </section>
            <section className="py-5 bg-primary">
                <div className="container">
                    <h1 className="text-center text-white mb-5"></h1>
                    <div className="row row-cols-1 row-cols-md-2 gx-md-5 gy-5">
                        <div data-aos="fade-down"
                            className="col">
                            <AdvantageCard
                                num={'01'}
                                icon={'bi bi-coin'}
                                title={''}
                                content={''}
                            />
                        </div>
                        <div data-aos="fade-down" className="col">
                            <AdvantageCard
                                num={'02'}
                                icon={'bi bi-arrows-angle-expand'}
                                title={''}
                                content={''} />
                        </div>
                        <div data-aos="fade-down" className="col">
                            <AdvantageCard
                                num={'03'}
                                icon={'bi bi-person-plus'}
                                title={''}
                                content={''} />
                        </div>
                        <div data-aos="fade-down" className="col">
                            <AdvantageCard
                                num={'04'}
                                icon={'bi bi-book'}
                                title={''}
                                content={''} />
                        </div>
                    </div>
                </div>
            </section>
        </>
    )
}