import { useForm } from "react-hook-form";
import { Input, Textarea, Checkbox, Radio } from "../../components/Input/Input";
import { useNavigate } from "react-router-dom";
import Alert from "../../components/Alert/Alert";
import { useState } from "react";
import PageTitle from "../../components/PageTitle/PageTitle";
import Loading from "../../components/Loading/Loading";

export default function Register() {
    const {
        register,
        handleSubmit,
        getValues,
        watch,
        formState: { errors }
    } = useForm({
        mode: 'onTouched'
    });
    const navigate = useNavigate();
    const [registerStatus, setRegisterStatus] = useState(false);
    const [isLoading,setIsLoading] = useState(false);
    const intro = watch("introduction");
    const onSubmit = (data) => {
        const { email, password, name, gender, phone, introdution,birth } = data;
        console.log(data);
        setIsLoading(true)
        setTimeout(()=>{
            setIsLoading(false);
            setRegisterStatus(true);
        },2000);
        
        setTimeout(() => {
            setRegisterStatus(false);
            navigate('/');
        }, 4000);
    }
    return (
        <>
            <section className="register container py-5 d-lg-flex flex-column align-items-center">
                <Alert alertTxt={''} color={'success'} status={registerStatus}></Alert>
                <Loading isLoading={isLoading}/>
                <PageTitle title={''}/>
                <form onSubmit={handleSubmit(onSubmit)}>
                    <Input
                        type='email'
                        id='email'
                        lableName={<>Email()<span className="text-danger ms-2">*</span></>}
                        placeholder='example@mail.com'
                        errors={errors}
                        register={register}
                        rules={{
                            required: 'Email ',
                            pattern: {
                                value: /^\S+@\S+$/i,
                                message: 'Email ',
                            }
                        }}
                    ></Input>
                    <Input
                        type='password'
                        id='password'
                        lableName={<>()<span className="text-danger ms-2">*</span></>}
                        placeholder='6'
                        errors={errors}
                        register={register}
                        rules={{
                            required: '',
                            minLength: {
                                value: 6,
                                message: ' 6 '
                            },
                        }}
                    ></Input>
                    <Input
                        type='password'
                        id='checkPW'
                        lableName={<>()<span className="text-danger ms-2">*</span></>}
                        placeholder='6'
                        errors={errors}
                        register={register}
                        rules={{
                            validate: (match) => {
                                const password = getValues('password');
                                return match === password || ''
                            }
                        }}
                    ></Input>
                    <Input
                        type='text'
                        id='name'
                        lableName={<>()<span className="text-danger ms-2">*</span></>}
                        placeholder=''
                        errors={errors}
                        register={register}
                        rules={{
                            required: ''
                        }}
                    ></Input>
                    <Input
                        type='text'
                        id='nickName'
                        lableName={''}
                        placeholder=''
                        errors={errors}
                        register={register}
                    ></Input>
                    <Input
                        type='date'
                        id='birth'
                        lableName={<>()<span className="text-danger ms-2">*</span></>}
                        
                        errors={errors}
                        register={register}
                        rules={{
                            required: ''
                        }}
                    ></Input>
                    <div className="genderWrapper">
                        <label> ()<span className="text-danger ms-2">*</span></label>
                        <div className="mt-2">
                            <Radio
                                id='male'
                                value='male'
                                name='gender'
                                labelName=''
                                errors={errors}
                                register={register}
                                rules={{
                                    required: ''
                                }}
                            />
                            <Radio
                                id='female'
                                value='female'
                                name='gender'
                                labelName=''
                                errors={errors}
                                register={register}
                                rules={{
                                    required: ''
                                }}
                            />
                        </div>
                    </div>
                    <Input
                        type='tel'
                        id='phone'
                        lableName={<>()<span className="text-danger ms-2">*</span></>}
                        placeholder='09xxxxxxxx'
                        errors={errors}
                        register={register}
                        rules={{
                            required: '',
                            pattern: {
                                value: /^09[0-9]{8}$/,
                                message: ''
                            },
                            minLength: {
                                value: 10,
                                message: ' 10 '
                            },
                            maxLength: {
                                value: 10,
                                message: ' 10 '
                            },
                        }}
                    ></Input>
                    <Textarea
                        id='introduction'
                        lableName={<>()<span className="text-danger ms-2">*</span></>}
                        row='4'
                        cols='50'
                        length={<>{intro ? intro.length:0}</>}
                        maxLen="200"
                        errors={errors}
                        register={register}
                        rules={{
                            required: '',
                            maxLength: {
                                value: 200,
                                message: ' 200 '
                            },
                        }}
                        placeholder=' 200 '
                    ></Textarea>
                    <Checkbox
                        id='agree'
                        labelName={
                            <> 
                                <button type="button" className="btn p-0 text-danger" style={{ textDecoration: 'underLine' }}></button>
                                <button type="button" className="btn p-0 text-danger" style={{ textDecoration: 'underLine' }}></button>
                                <button type="button" className="btn p-0 text-danger" style={{ textDecoration: 'underLine' }}></button>
                                
                            </> 
                            // <>
                            //     <a className=" text-primary" style={{ textDecoration: 'underLine', cursor:"pointer" }}></a>
                            //     <a className=" text-primary" style={{ textDecoration: 'underLine' , cursor:"pointer" }}></a>
                            //     <a className=" text-primary" style={{ textDecoration: 'underLine' , cursor:"pointer" }}></a>
                            //     
                            // </>
                        }
                        errors={errors}
                        register={register}
                        rules={{
                            required: ''
                        }}
                    />
                    <div className="d-flex justify-content-center">
                        <button type="submit" className="btn btn-primary px-5 py-3 registerBtn"></button>
                    </div>
                </form>
            </section>

        </>
    )
}