import { Input, Textarea } from "../../components/Input/Input";
import Alert  from "../../components/Alert/Alert";
import { useState } from "react";
import { useForm } from "react-hook-form";
import PageTitle from "../../components/PageTitle/PageTitle";
export default function Contact() {
    const {
        register,
        handleSubmit,
        reset,
        watch,
        formState: { errors }
    } = useForm({
        mode: 'onTouched'
    });

    const [ isSend,setIsSend ] = useState(false);
    const message = watch("message");

    const onSubmit = (data) => {
        const { name , title, email, phone, message}= data;
        console.log(data);
        setIsSend(true);
        reset();
        setTimeout(()=>{
           setIsSend(false);
        },5000);
        
    }
    return (
        <section className="contact container py-5 d-lg-flex flex-column align-items-center">
            <Alert alertTxt={''} color={'success'} status={isSend}> </Alert>
            <PageTitle title={''} />
            <form onSubmit={handleSubmit(onSubmit)}>
                <Input
                    type='text'
                    id='name'
                    lableName={<>()<span className="text-danger ms-2">*</span></>}
                    errors={errors}
                    register={register}
                    rules={{
                        //required: ""
                    }}
                ></Input>
                <Input
                    type='text'
                    id='title'
                    lableName={<>()<span className="text-danger ms-2">*</span></>}
                    errors={errors}
                    register={register}
                    rules={{
                        //required: ""
                    }}
                ></Input>
                <Input
                    type='email'
                    id='email'
                    lableName={<>Email()<span className="text-danger ms-2">*</span></>}
                    errors={errors}
                    register={register}
                    rules={{
                        //required: "Email"
                    }}
                ></Input>
                <Input
                    type='tel'
                    id='phone'
                    lableName={<>()<span className="text-danger ms-2">*</span></>}
                    errors={errors}
                    register={register}
                    rules={{
                        //required: "",
                        pattern: {
                            value: /^09[0-9]{8}$/,
                            message: ''
                        },
                        minLength: {
                            value: 10,
                            message: ' 10 '
                        },
                        maxLength: {
                            value: 10,
                            message: ' 10 '
                        },
                    }}
                ></Input>
                <Textarea
                        id='message'
                        lableName={<>()<span className="text-danger ms-2">*</span></>}
                        row='4'
                        cols='50'
                        length={<>{message? message.length:0}</>}
                        maxLen="200"
                        errors={errors}
                        register={register}
                        rules={{
                            //required: '',
                            maxLength: {
                                value: 200,
                                message: ' 200 '
                            },
                        }}
                        placeholder=' 200 '
                    ></Textarea>
                    <div className="d-flex justify-content-center">
                        <button type="submit" className="btn btn-primary w-100 py-3 registerBtn"></button>
                    </div>
            </form>
        </section>
    )
}